import css from './UsersPage.module.css'
import React, { useEffect } from 'react'
import Users from './Users'
import Preloader from '../../plugins/Preloader/Preloader'


function UsersPageC(props) {
    useEffect(() => {
        props.getUsers(props.usersToShow, props.currentPage)
    },[])

    let pageChange = (currentPage) => {
        props.getUsers(props.usersToShow, currentPage)
    }

    return <div className={css.users__container}>
        {
            props.isLoading ? <Preloader /> : <Users
            totalUsersCount={props.totalUsersCount}
            users={props.users}
            usersToShow={props.usersToShow}
            currentPage={props.currentPage}
            pageChange={pageChange}
            follow={props.follow}
            unFollow={props.unFollow}
            {...props}
        />}
    </div>
}



export default UsersPageC


