import { createSelector } from '@reduxjs/toolkit'

let getUsers = (state) => {
    return state.usersPage.users
}
export let getUsersSelector = createSelector(getUsers, users => users)

export let getTotalUsersCount = (state) => {
    return state.usersPage.totalUsersCount
}

export let getUsersToShow = state => {
    return state.usersPage.usersToShow
}

export let getCurrentPage = state => {
    return state.usersPage.currentPage
}

export let getIsLoading = state => {
    return state.usersPage.isLoading
}

export let getFollowingInProcessUsers = state => {
    return state.usersPage.followingInProcessUsers
}