import { connect } from "react-redux";
import { follow, getUsers, setCurrentPage, setFollowingProcess, setIsLoading, setTotalUsersCount, setUsersAC, unFollow, unFollowAC } from "../../redux/usersPageReducer";
import UsersPageC from "./UsersPageС";
import { withAuthRedirect } from "../../hoc/Redirect";
import { compose } from "redux";
import { getCurrentPage, getFollowingInProcessUsers, getIsLoading, getTotalUsersCount, getUsersSelector, getUsersToShow } from "../../redux/users-selectors";



let mapStateToProps = (state) => {
    return {
        users: getUsersSelector(state),
        totalUsersCount: getTotalUsersCount(state),
        usersToShow: getUsersToShow(state),
        currentPage: getCurrentPage(state),
        isLoading: getIsLoading(state),
        followingInProcessUsers: getFollowingInProcessUsers(state),
    }
}

let mapDispatchToProps = (dispatch) => {
    return {
        setUsers: (users) => {
            dispatch(setUsersAC(users))
        },
        setTotalUsersCount: (usersCount) => {
            dispatch(setTotalUsersCount(usersCount))
        },
        setCurrentPage: (currentPage) => {
            dispatch(setCurrentPage(currentPage))
        },
        follow: (userID) => {
            dispatch(follow(userID))
        },
        unFollow: (userID) => {
            dispatch(unFollow(userID))
        },
        setIsLoading: (bool) => {
            dispatch(setIsLoading(bool))
        },
        setFollowingProcess: (isFetching, userID) => {
            dispatch(setFollowingProcess(isFetching, userID))
        },
        getUsers: (usersToShow, currentPage) => {
            dispatch(getUsers(usersToShow, currentPage))
        }
     }
}

let UsersPageContainer = compose(
    withAuthRedirect,
    connect(mapStateToProps,mapDispatchToProps)
    )(UsersPageC)

export default UsersPageContainer