import { NavLink } from 'react-router-dom'
import css from './UsersPage.module.css'
import Pagination from '@mui/material/Pagination';
import { useEffect, useState } from 'react';

let User = ({ ava, name, location, status = 'No status', followed, follow, unFollow, id, ...props }) => {
    let followUser = (userID) => {
        follow(userID)
    }

    let unFollowUser = userID => {
        unFollow(userID)
    }



    return (
        <div className={css.user__container}>
            <div className={css.left__block}>
                <div className={css.user__ava}>
                    <NavLink to={`profile/${id}`}>
                        <img src={ava ? ava : 'https://marketplace.canva.com/EAFEits4-uw/1/0/800w/canva-boy-cartoon-gamer-animated-twitch-profile-photo-r0bPCSjUqg0.jpg'} alt="" />
                    </NavLink>
                </div>
                <div className={css['user__follow-btn']}>
                    {
                        followed ?
                            <button className={css.followed__button} disabled={props.followingInProcessUsers.some(userID => userID == id)} onClick={() => unFollowUser(id)}>Unfollow</button>
                            :
                            <button className={css.unfollowed__button} disabled={props.followingInProcessUsers.some(userID => userID == id)} onClick={() => followUser(id)}>Follow</button>
                    }
                </div>
            </div>
            <div className={css['right-block']}>
                <div className={css.user__info}>
                    <div className={css.info__left__block}>
                        <div className={css.user__name}>
                            {name}
                        </div>
                        <div className={css.user__status}>
                            {status ? status : 'No status'}
                        </div>
                    </div>
                    <div className={css.info__right__block}>
                        <div className={css.user__location}>
                            <div className={css.location__city}>Erevan</div>
                            <div className={css.location__country}>Armenia</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}




function Users({ totalUsersCount, usersToShow, currentPage, pageChange, users, follow, unFollow, ...props }) {
    let pagesCount = Math.ceil(totalUsersCount / usersToShow)
    // let [currentPage, setCurrentPage] = useState(page)
    // useEffect(() => {
    //     pageChange(currentPage)
    // },[currentPage])
    // let pages = []

    // for (let i = 1; i <= pagesCount; i++) {
    //     pages.push(i)
    // }

    let handler = (e, value) => {
        // setCurrentPage(value)
        pageChange(value)
    }

    return (
        <div className={css.users__container}>
            <div className={css.users__content}>
                <h1>Users</h1>
                <div className={css.pagination__container}>
                    {/* <div className={css.pages__container}>
                        {
                            pages.map((elem, index) => {
                                return <div key={elem} className={currentPage === elem ? css.active : undefined} onClick={() => pageChange(elem)}>{elem}</div>
                            })
                        }
                    </div> */}
                    <Pagination count={pagesCount} defaultPage={currentPage} boundaryCount={10} onChange={handler}/>

                </div>
                {users.map((elem, index) => {
                    return <User
                        {...props}
                        id={elem.id}
                        follow={follow}
                        unFollow={unFollow}
                        key={elem.id}
                        followed={elem.followed}
                        ava={elem.photos.small}
                        status={elem.status}
                        name={elem.name} />
                })}
            </div>
        </div>
    )
}

export default Users;